* {
	margin: 0;
}

.app {
	background-color: #fafafa;
	padding-bottom: 100px;
}

.app__login {
	display: flex;
	flex-direction: column;
	height: 250px;
	justify-content: center;
	padding: 20px;
	background-color: white;
	border: 1px solid lightgray;
}

.effect_loginBOX {
	-webkit-box-shadow: 0 10px 6px -6px #777;
	-moz-box-shadow: 0 10px 6px -6px #777;
	box-shadow: 0 5px 6px -6px #777;
}

.app__loginHome {
	display: flex;
	flex-direction: row;
	justify-content: center;
	padding: 20px;
	background-color: white;
	border: 1px solid lightgray;
}

.app__upload {
	position: fixed;
	bottom: 0;
	width: 100%;
	background-color: white;
	border-top: 1px solid lightgray;
	text-align: center;
	padding: 20px;
	display: flex;
	justify-content: center;
}

.app__headerImage {
	object-fit: contain;
}

.app__headerRight {
	display: flex;
	align-items: center;
}

.app__headerRight > p {
	margin-right: 5px;
}

.app__headerAvatar {
	height: 30px !important;
	width: 30px !important;
}

.app__header {
	position: sticky;
	top: 0;
	display: flex;
	justify-content: space-between;
	z-index: 1;
	padding: 20px;
	background-color: #ffffff;
	border-bottom: 1px solid lightgray;
	margin-bottom: 10px;
}

.effect_header {
	-webkit-box-shadow: 0 10px 6px -6px #777;
	-moz-box-shadow: 0 10px 6px -6px #777;
	box-shadow: 0 5px 6px -6px #777;
}

.app__posts {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
	padding: 10px 30px;
}

.app__postsRight {
	margin-left: 10px;
}

.app__postsRight_Effect {
	-webkit-box-shadow: 0 10px 6px -6px #777;
	-moz-box-shadow: 0 10px 6px -6px #777;
	box-shadow: 0 5px 6px -6px #777;
}

/* Footer */

footer {
	padding-top: 48px;
	margin-bottom: -50px;
	color: #303030;
	font-size: 15px;
	text-align: center;
	position: relative;
	text-transform: uppercase;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
		Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
